<template>
  <div class="middle-con" id="danmu-wrap">
   
    </div>
  </div>
</template>
<script>
import bus from "../bus/danmubus";
import danmu from "./js/danmu";
import $ from "jquery";
export default {
  name: "home",
  data: function() {
    return {
      list: [],
      newTextFlag: false
    };
  },
  methods: {
    getDanMu: function(text) {
      $.ajax({
        url: "http://localhost:29212/api/danmu",
        method: "GET",
        dataType: "json",
        data: {},
        success: function(data) {
          console.log(data);
        }
      });
    },
    initSocket: function(socket) {
      //
      var ws = location.protocol === "https" ? "wss" : "ws"; // https  wss
      var address =
        process.env.NODE_ENV === "development" ? "localhost:29212" : "106.14.172.153:8082";
      var uri = ws + "://" + address + "/ws";

      var time = null;
      var timedCount = function() {
        if (socket.readyState === 1)
          //1 - 
          socket.send("heartbeat");
        time = setTimeout(timedCount, 58 * 1000); //58
      };
      if (socket === null || socket.readyState != 1) {
        //1 - 
        socket = new WebSocket(
          uri + "?userName=" + parseInt(Math.random() * 100)
        );
        timedCount();
      }
      socket.onopen = function() {};
      //
      socket.onmessage = (e)=> {
        
        var data = JSON.parse(e.data);
        this.list.push(data.Text);
        this.newTextFlag=true;
      };
      return socket;
    }
  },
  created: function() {
    this.getDanMu();
    var socket = null;
    socket = this.initSocket(socket);
    bus.$on("sendDanMu", text => {
      if (!!!text) return;
      socket.send(JSON.stringify({ Text: text }));
      this.list.push(text);
      this.newTextFlag = true;
    });

    this.damuTicker = setInterval(() => {
      if (!this.newTextFlag) return false;
      let text = this.list[this.list.length - 1];
      let damu = new danmu("danmu-wrap", text, 20000);
      this.newTextFlag = false;
    }, 500);
  },
  destroyed: function() {
    clearInterval(this.damuTicker);
  }
};
</script>
<style >
.middle-con {
  background-color: rgba(255, 255, 255, 0.1);
  width: 80%;
}
#danmu-wrap {
  position: relative;
  overflow: hidden;
}

.danmu {
  position: absolute;
  padding: 5px 10px;
  background: rgba(255, 255, 255, 0.4);
  word-break: keep-all; /*  */
  white-space: nowrap; /*  */
  border-radius: 8px;
  display: inline-block;
  transition: right 20s cubic-bezier(0, 0, 1, 1);
  color: #000;
  right: -200px;
  font-size: 18px;
}
</style>






